import React, { useState, useLayoutEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useForm, Form } from '../../shared/useForm';
import Controls from '../../components/Controls/Controls';
import classes from './ModifyUsers.module.css';
import Spinner from '../../components/UI/Spinner/Spinner';

import axios from 'axios';

const initialValues = {
  name: '',
  firstLastName: '',
  secondLastName: '',
  rfc: '',
  newRfc: '',
  email: '',
  phoneNumber: '',
  plaza: '',
  gender: '',
  department: '',
  userType: '',
};

const getGender = () => [
  { id: 'F', title: 'F' },
  { id: 'M', title: 'M' },
];

const getDepartment = () => [
  {
    id: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
    title: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
  },
  { id: 'SISTEMAS E INFORMÁTICA', title: 'SISTEMAS E INFORMÁTICA' },
  { id: 'METAL MECÁNICA Y AERONÁUTICA', title: 'METAL MECÁNICA Y AERONÁUTICA' },
  { id: 'INDUSTRIAL', title: 'INDUSTRIAL' },
  { id: 'CIENCIAS BÁSICAS', title: 'CIENCIAS BÁSICAS' },
  { id: 'ECONÓMICO ADMINISTRATIVO', title: 'ECONÓMICO ADMINISTRATIVO' },
];

const getUserTypes = () => [
  // { id: 'PARTICIPANTE', title: 'PARTICIPANTE' },
  { id: 'INSTRUCTOR', title: 'INSTRUCTOR' },
  { id: 'COORDINADOR', title: 'COORDINADOR' },
];

const ModifyUsers = (props) => {
  const [loading, setLoading] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/api/get-user-data/'
    axios
      .get('/api/get-user-data/' + props.rowData)
      .then((response) => {
        setValues({
          name: response.data.name,
          firstLastName: response.data.firstLastName,
          secondLastName: response.data.secondLastName,
          rfc: response.data.rfc,
          newRfc: '',
          email: response.data.email,
          phoneNumber: response.data.phoneNumber,
          plaza: response.data.plaza,
          gender: response.data.gender,
          department: response.data.department,
          userType: response.data.userType,
        });
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const validate = (fieldValues = values) => {
    let temp = { ...errors };

    if ('newRfc' in fieldValues)
      temp.newRfc =
        fieldValues.newRfc.length >= 1 && fieldValues.newRfc.length < 12
          ? 'RFC debe contener al menos 12 caracteres'
          : '';

    if ('email' in fieldValues)
      temp.email = /$^|.+@.+..+/.test(fieldValues.email)
        ? ''
        : 'Email no valido';

    if ('phoneNumber' in fieldValues)
      temp.phoneNumber = /^(\d{10}|\d{11}|\d{12})$/.test(
        fieldValues.phoneNumber
      )
        ? ''
        : 'Número debe contener entre 10 y 12 dígitos';

    setErrors({
      ...temp,
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == '');
  };

  const { values, errors, setErrors, handleInputChange, setValues } = useForm(
    initialValues,
    true,
    validate
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      let post = {
        name: values.name,
        firstLastName: values.firstLastName,
        secondLastName: values.secondLastName,
        rfc: values.rfc,
        newRfc: values.newRfc,
        email: values.email,
        phoneNumber: values.phoneNumber,
        plaza: values.plaza,
        gender: values.gender,
        department: values.department,
        userType: values.userType,
      };
      //let url ='/auth/modify-users'
      axios
        .put('/auth/modify-users', post)
        .then((response) => {
          props.closePopup(false);
        })
        .catch((error) => {
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            window.alert('Error: ' + error.response.data.message);
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
    }
  };

  let form = (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        <Grid item xs={6}>
          <Controls.Input
            className={classes.rfc}
            disabled
            name='rfc'
            label='RFC actual'
            placeholder='RFC actual'
            value={values.rfc}
            onChange={handleInputChange}
            error={errors.rfc}
            inputProps={{ maxLength: 13 }}
          />

          <Controls.Input
            className={classes.rfc}
            name='newRfc'
            label='Nuevo RFC'
            placeholder='Nuevo RFC'
            value={values.newRfc.replace(/\s/g, '')}
            onChange={handleInputChange}
            error={errors.newRfc}
            inputProps={{ maxLength: 13 }}
          />
          <Controls.Input
            name='name'
            label='Nombre'
            placeholder='Nombre'
            value={values.name}
            onChange={handleInputChange}
            error={errors.name}
            inputProps={{ maxLength: 50 }}
          />
          <Controls.Input
            name='firstLastName'
            label='Apellido paterno'
            placeholder='Apellido paterno'
            value={values.firstLastName}
            onChange={handleInputChange}
            error={errors.firstLastName}
            inputProps={{ maxLength: 20 }}
          />
          <Controls.Input
            name='secondLastName'
            label='Apellido materno'
            placeholder='Apellido materno'
            value={values.secondLastName}
            onChange={handleInputChange}
            error={errors.secondLastName}
            inputProps={{ maxLength: 20 }}
          />
          <Controls.Input
            name='email'
            label='Email'
            placeholder='Email'
            value={
              values.email ? values.email.replace(/\s/g, '') : values.email
            }
            onChange={handleInputChange}
            inputProps={{ maxLength: 100 }}
            error={errors.email}
          />
        </Grid>
        <Grid item xs={6}>
          <Controls.Input
            name='phoneNumber'
            label='Telefono'
            placeholder='Telefono'
            type='number'
            value={values.phoneNumber}
            onChange={handleInputChange}
            error={errors.phoneNumber}
            inputProps={{
              onWheel: (event) => event.currentTarget.blur(),
              min: 0,
            }}
          />
          <Controls.Input
            name='plaza'
            label='Plaza'
            placeholder='Plaza'
            value={values.plaza.replace(/\s/g, '')}
            onChange={handleInputChange}
            inputProps={{ maxLength: 15 }}
          />
          <Controls.Select
            name='gender'
            label='Genero'
            displayEmpty
            onChange={handleInputChange}
            value={values.gender}
            options={getGender()}
          />
          <Controls.Select
            name='department'
            label='Departamento'
            displayEmpty
            onChange={handleInputChange}
            value={values.department}
            options={getDepartment()}
          />
          <Controls.Select
            name='userType'
            label='Tipo de usuario'
            displayEmpty
            onChange={handleInputChange}
            value={values.userType}
            options={getUserTypes()}
          />

          <Button
            type='submit'
            fullWidth
            variant='contained'
            color='primary'
            style={{ marginTop: '15px' }}
          >
            ACEPTAR
          </Button>
        </Grid>
      </Grid>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }
  return <div>{form}</div>;
};

export default ModifyUsers;
