import React, { useState } from 'react';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import Input from '../../components/Controls/Input';
import Select from '../../components/Controls/Select';
import Grid from '@material-ui/core/Grid';
import { Form, useForm } from '../../shared/useForm';
import Spinner from '../../components/UI/Spinner/Spinner';
import SnackBar from '../../components/UI/SnackBar/SnackBar';
import classes from './RegisterUsers.module.css';

import axios from 'axios';

const initialValues = {
  rfc: '',
  confirmRfc: '',
  userType: '',
};

const getUserTypes = () => [
  //{ id: 'PARTICIPANTE', title: 'PARTICIPANTE' },
  { id: 'INSTRUCTOR', title: 'INSTRUCTOR' },
  { id: 'COORDINADOR', title: 'COORDINADOR' },
];

const UserConfig = (props) => {
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const [loading, setLoading] = useState(false);
  const { auxRegister, setAuxRegister } = props;

  const validate = (fieldValues = values) => {
    let temp = { ...errors };

    if (values.rfc) {
      temp.rfc = '';
    } else {
      temp.rfc = 'Campo obligatorio';
    }

    if (values.confirmRfc) {
      temp.confirmRfc = '';
    } else {
      temp.confirmRfc = 'Campo obligatorio';
    }

    if (values.userType) {
      temp.userType = '';
    } else {
      temp.userType = 'Debe elegir un tipo de usuario';
    }

    if (values.rfc.toUpperCase() == values.confirmRfc.toUpperCase()) {
      if (values.rfc.length < 12 || values.confirmRfc.length < 12) {
        temp.rfc = 'RFC debe contener al menos 12 caracteres';
        temp.confirmRfc = 'RFC debe contener al menos 12 caracteres';
      } else {
        temp.rfc = '';
        temp.confirmRfc = '';
      }
    } else if (values.rfc.toUpperCase() != values.confirmRfc.toUpperCase()) {
      temp.rfc = 'Los RFC no coinciden';
      temp.confirmRfc = 'Los RFC no coinciden';
    }

    setErrors({
      ...temp,
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == '');
  };

  const { values, resetForm, errors, setErrors, handleInputChange } = useForm(
    initialValues,
    true,
    validate
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      setLoading((loading) => !loading);
      let post = {
        rfc: values.rfc,
        userType: values.userType,
      };
      //let url ='/auth/register-users'
      axios
        .post(
          '/auth/register-users',
          post
        )
        .then((response) => {
          setLoading((loading) => !loading);
          setAuxRegister((auxRegister) => !auxRegister);
          setSnackStatus({
            openSnackBar: true,
            severity: 'success',
            snackBarMessage: 'Usuario registrado correctamente',
          });
        })
        .catch((error) => {
          setLoading((loading) => !loading);
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            setSnackStatus({
              openSnackBar: true,
              severity: 'error',
              snackBarMessage: error.response.data.message,
            });
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
      resetForm(initialValues);
    }
  };

  let form = (
    <Form className={classes.form} onSubmit={handleSubmit}>
      <h3>Registrar Usuario</h3>
      <Grid container>
        <Grid item xs={6}>
          <Input
            labelHeight={0}
            name='rfc'
            placeholder='RFC'
            label='RFC'
            value={values.rfc.replace(/\s/g, '')}
            onChange={handleInputChange}
            inputProps={{ maxLength: 13 }}
            error={errors.rfc}
          />

          <Input
            name='confirmRfc'
            placeholder='Confirmar RFC'
            label='Confirmar RFC'
            value={values.confirmRfc.replace(/\s/g, '')}
            onChange={handleInputChange}
            inputProps={{ maxLength: 13 }}
            error={errors.confirmRfc}
          />
        </Grid>
        <Grid item xs={6}>
          <Select
            className={classes.formControl}
            name='userType'
            label='Tipo de usuario'
            displayEmpty
            onChange={handleInputChange}
            value={values.userType}
            error={errors.userType}
            options={getUserTypes()}
          />
        </Grid>
      </Grid>

      <Button
        className={classes.submit}
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
      >
        REGISTRAR USUARIO
      </Button>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }

  return (
    <div className={classes.paper}>
      {form}{' '}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.auth.token,
  };
};

export default connect(mapStateToProps)(UserConfig);
