import { React, useState, useLayoutEffect, Fragment } from 'react';
import MaterialTable from 'material-table';
import RegisterUsers from './RegisterUsers';
import ModifyUsers from './ModifyUsers';
import Popup from '../../components/UI/Popup/Popup';
import Spinner from '../../components/UI/Spinner/Spinner';
import SnackBar from '../../components/UI/SnackBar/SnackBar';
import axios from 'axios';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const CoursesOverview = (props) => {
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [rfc, setRfc] = useState([]);
  const [auxRegister, setAuxRegister] = useState(false);
  const [openModify, setOpenModify] = useState(false);
  const [isDeleted, setIsDeleted] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, [openModify, auxRegister, isDeleted]);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/api/users-overview'
    axios
      .get('/api/users-overview')
      .then((response) => {
        setData(response.data);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const restorePasswordHandler = (rfc, name) => {
    var value = window.confirm('Reestablecer contraseña de: ' + name + '?');
    if (value == true) {
      //url = /auth/restore-password/
      axios
        .put('/auth/restore-password/' + rfc)
        .then((res) => {
          setSnackStatus({
            openSnackBar: true,
            severity: 'success',
            snackBarMessage: 'Contraseña restablecida correctamente',
          });
        })
        .catch((error) => {
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            setSnackStatus({
              openSnackBar: true,
              severity: 'error',
              snackBarMessage: error.response.data.message,
            });
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
      return true;
    } else {
      return false;
    }
  };

  const deleteUserHandler = (rfc, name) => {
    var value = window.confirm('Eliminar usuario: ' + name + '?');
    if (value == true) {
      //url = /auth/delete-user/
      axios
        .delete('/auth/delete-user/' + rfc)
        .then((res) => {
          setIsDeleted((isDeleted) => !isDeleted);
          setSnackStatus({
            openSnackBar: true,
            severity: 'success',
            snackBarMessage: 'Usuario eliminado correctamente',
          });
        })
        .catch((error) => {
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            setSnackStatus({
              openSnackBar: true,
              severity: 'error',
              snackBarMessage: error.response.data.message,
            });
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
      return true;
    } else {
      return false;
    }
  };

  let registerAndModifyUsers = (
    <Fragment>
      <RegisterUsers
        auxRegister={auxRegister}
        setAuxRegister={setAuxRegister}
      />
      <Popup
        title='Modificar Usuario'
        openPopup={openModify}
        setOpenPopup={setOpenModify}
      >
        <ModifyUsers rowData={rfc} closePopup={setOpenModify} />
      </Popup>
    </Fragment>
  );

  let usersTable = (
    <Fragment>
      <MaterialTable
        title='Usuarios'
        columns={[
          { title: 'Nombre', field: 'name' },
          { title: 'RFC', field: 'rfc' },
          { title: 'Correo', field: 'email' },
          { title: 'Permisos', field: 'userType' },
        ]}
        data={data}
        actions={[
          {
            icon: 'delete',
            iconProps: { style: { color: '#B70000' } },
            tooltip: 'Eliminar Usuario',
            onClick: (event, rowData) => {
              deleteUserHandler(rowData.rfc, rowData.name);
            },
          },
          {
            icon: 'edit',
            iconProps: { style: { color: '#115293' } },
            tooltip: 'Editar/Mas información',
            onClick: (event, rowData) => {
              setRfc(rowData.rfc);
              setOpenModify((openModify) => !openModify);
            },
          },
          {
            icon: 'change_circle',
            tooltip: 'Reestablecer Contraseña',
            onClick: (event, rowData) => {
              restorePasswordHandler(rowData.rfc, rowData.name);
            },
          },
        ]}
        options={{
          pageSize: 50,
          pageSizeOptions: [50, 75, 100],
          headerStyle: {
            backgroundColor: '#808080',
            color: '#FFF',
            fontSize: '15px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'Acciones',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No hay usuarios registrados',
          },
        }}
      />
    </Fragment>
  );

  if (loading) {
    usersTable = <Spinner />;
  }

  return (
    <div style={styles}>
      {registerAndModifyUsers}
      {usersTable}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

export default CoursesOverview;
